/* src/styles.css */
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Sinhala:wght@400;600&display=swap');

body {
    font-family: 'Noto Serif Sinhala', serif;
    background-color: #f8f8f8;
    margin: 0;
    padding-top: 5em;
    display: flex;
    justify-content: center;
    height: 100vh;
  }
  
  div {
    text-align: center;
  }
  
  input {
    font-family: 'Noto Serif Sinhala', serif;
    padding: 10px;
    font-size: 16px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  li {
    padding: 8px;
    margin: 5px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  li:hover {
    background-color: #f0f0f0;
  }
  